import React from "react";
import { Row, Col, Container, Image } from "react-bootstrap";
import "../styles/css/About.css";
import synth from "../assets/synth.gif";
const About = () => {
    return (
        <Container className="about-wrapper">
            <Row className="main-row about-row">
                <Col className="about-me-col" lg={9}>
                    <div className="about-me">
                        <h3>Quem é esse Fábio? </h3>
                        <h3>Homem, Mito ou Desenvolvedor Lendário?</h3>
                        <div className="bio">
                            <p className="bio-paragraph-1">
                                - Fábio é um estudante de Ciência da Computação
                                e desenvolvedor web em constante
                                aperfeiçoamento. Ele desenvolve projetos
                                principalmente em JavaScript.
                            </p>
                            <p className="bio-paragraph-1">
                                - Quando Fábio não está programando e/ou ouvindo
                                música, provavelmente está fazendo o que algumas
                                pessoas pouco ajuizadas chamam de "música"; ou
                                levantanto pesos que as vezes o fazem vomitar :(
                            </p>
                            <p className="bio-paragraph-1">
                                - E por que ele faz isso?
                            </p>
                            <p className="bio-paragraph-1">
                                - Provavelmente por razões que ele não
                                conseguiria articular claramente.
                            </p>
                            <p className="bio-paragraph-2">
                                Chega de falar de mim mesmo em terceira pessoa.
                            </p>
                            <p className="bio-paragraph-2">
                                Você pode continuar rolando e descobrir o que eu
                                faço. <br />
                                Ou conferir a playlist com músicas que eu gosto
                                de ouvir enquanto eu faço o que eu faço. <br />O
                                que eu faço você só vai descobrir se continuar
                                rolando e não perder tempo ouvindo música no
                                site de um desenvolvedor.
                            </p>
                            <p className="bio-paragraph-2">
                                <i>Namaste.</i>
                            </p>
                            <p className="signature">Fábio B. Ribeiro</p>
                        </div>
                    </div>
                </Col>

                <Col className="playlist" lg={3}>
                    <Image className="synth" src={synth} />
                    <iframe
                        src="https://open.spotify.com/embed/playlist/6HhLuT81SJgLqPvBHjn3Jx"
                        width="300"
                        height="380"
                        frameborder="0"
                        allowtransparency="true"
                        allow="encrypted-media"
                    ></iframe>
                </Col>
            </Row>
        </Container>
    );
};

export default About;
